@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,600");

:root {
    --primary: #396dff;
    --primary--dim: #6987db;
    --darkgrey: #d4d4d4;
    --grey: #f7f7f7;
    --white: #fff;

    --xs: 4px;
    --s: 8px;
    --m: 16px;
    --l: 24px;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Open Sans", sans-serif;
    font-weight: 300;
}

form {
    flex-grow: 1;
}

.u-flex {
    display: flex;
}

.u-flex-justifyCenter {
    justify-content: center;
}

.u-flex-alignCenter {
    align-items: center;
}

.u-inlineBlock {
    display: inline-block;
}

.u-bold {
    font-weight: 600;
}

.u-textCenter {
    text-align: center;
}

.u-pointer {
    cursor: pointer;
}

.u-link {
    color: var(--primary);
    text-decoration: none;
    cursor: pointer;
}

.u-link:hover {
    color: var(--primary--dim);
}