.NavBar-container {
    padding: var(--s) var(--m);
    background-color: var(--primary);
}

.NavBar-title {
    color: var(--white);
    font-size: 20px;
}

.NavBar-linkContainer {
    margin-left: var(--l);
}

.NavBar-link,
.NavBar-link:visited {
    color: var(--white);
    opacity: 0.6;
    text-decoration: none;
}

.NavBar-link:hover {
    opacity: 0.8;
}

.NavBar-link+.NavBar-link {
    margin-left: var(--m);
}